	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"gatsby-transformer-xml\"><a href=\"#gatsby-transformer-xml\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>gatsby-transformer-xml</h1>\n<p>Parses XML files. It supports also attributes</p>\n<h2 id=\"install\"><a href=\"#install\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Install</h2>\n<p><code>npm install --save gatsby-transformer-xml</code></p>\n<h2 id=\"how-to-use\"><a href=\"#how-to-use\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How to use</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token comment\">// In your gatsby-config.js</span>\nplugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token template-string\"><span class=\"token string\">`gatsby-transformer-xml`</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<h2 id=\"parsing-algorithm\"><a href=\"#parsing-algorithm\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Parsing algorithm</h2>\n<p>The algorithm for arrays is to convert each item in the array into\na node.</p>\n<p>So if your project has a <code>bookss.xml</code> with</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-xml\"><code><span class=\"token prolog\">&lt;?xml version=&quot;1.0&quot;?&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>catalog</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>book</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>bk101<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>author</span><span class=\"token punctuation\">&gt;</span></span>Gambardella, Matthew<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>author</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>title</span><span class=\"token punctuation\">&gt;</span></span>XML Developer&apos;s Guide<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>title</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>genre</span><span class=\"token punctuation\">&gt;</span></span>Computer<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>genre</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>price</span><span class=\"token punctuation\">&gt;</span></span>44.95<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>price</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>publish_date</span><span class=\"token punctuation\">&gt;</span></span>2000-10-01<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>publish_date</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>description</span><span class=\"token punctuation\">&gt;</span></span>An in-depth look at creating applications \n      with XML.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>description</span><span class=\"token punctuation\">&gt;</span></span>\n   <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>book</span><span class=\"token punctuation\">&gt;</span></span>\n   <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>book</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>bk102<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>author</span><span class=\"token punctuation\">&gt;</span></span>Ralls, Kim<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>author</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>title</span><span class=\"token punctuation\">&gt;</span></span>Midnight Rain<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>title</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>genre</span><span class=\"token punctuation\">&gt;</span></span>Fantasy<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>genre</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>price</span><span class=\"token punctuation\">&gt;</span></span>5.95<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>price</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>publish_date</span><span class=\"token punctuation\">&gt;</span></span>2000-12-16<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>publish_date</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>description</span><span class=\"token punctuation\">&gt;</span></span>A former architect battles corporate zombies, \n      an evil sorceress, and her own childhood to become queen \n      of the world.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>description</span><span class=\"token punctuation\">&gt;</span></span>\n   <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>book</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>catalog</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n      </div>\n<p>Then the following 2 nodes will be created</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">{</span>\n   <span class=\"token string\">&quot;root&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n      <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;catalog&quot;</span><span class=\"token punctuation\">,</span>\n      <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n      <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n         <span class=\"token punctuation\">{</span>\n            <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;book&quot;</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n               <span class=\"token string\">&quot;id&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;bk101&quot;</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n               <span class=\"token punctuation\">{</span>\n                  <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;author&quot;</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n\n                  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n\n                  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;Gambardella, Matthew&quot;</span>\n               <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n               <span class=\"token punctuation\">{</span>\n                  <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;title&quot;</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n\n                  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n\n                  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;XML Developer&apos;s Guide&quot;</span>\n               <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;&quot;</span>\n         <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n         <span class=\"token punctuation\">{</span>\n            <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;book&quot;</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n               <span class=\"token string\">&quot;id&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;bk102&quot;</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n               <span class=\"token punctuation\">{</span>\n                  <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;author&quot;</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n\n                  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n\n                  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;Ralls, Kim&quot;</span>\n               <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n               <span class=\"token punctuation\">{</span>\n                  <span class=\"token string\">&quot;name&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;title&quot;</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;attributes&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">{</span>\n\n                  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;children&quot;</span><span class=\"token punctuation\">:</span><span class=\"token punctuation\">[</span>\n\n                  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n                  <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;Midnight Rain&quot;</span>\n               <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n            <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;&quot;</span>\n         <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n      <span class=\"token string\">&quot;content&quot;</span><span class=\"token punctuation\">:</span><span class=\"token string\">&quot;&quot;</span>\n   <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h2 id=\"how-to-query\"><a href=\"#how-to-query\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How to query</h2>\n<p>You’d be able to query your books like:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-graphql\"><code><span class=\"token punctuation\">{</span>\n  allBooks <span class=\"token punctuation\">{</span>\n    edges <span class=\"token punctuation\">{</span>\n      node <span class=\"token punctuation\">{</span>\n        content\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Which would return:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">{</span>\n  allBooks<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    edges<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n      <span class=\"token punctuation\">{</span>\n        node<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n          content<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;Gambardella, Matthew&apos;</span>\n        <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">{</span>\n        node<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n          content<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;XML Developer&apos;</span>s Guide\n        <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>","excerpt":"gatsby-transformer-xml Parses XML files. It supports also attributes Install npm install --save gatsby-transformer-xml How to use Parsing…","timeToRead":2,"fields":{"title":"gatsby-transformer-xml"},"parent":{"__typename":"File","relativePath":"gatsby-transformer-xml/README.md"}}},"pathContext":{"slug":"/packages/gatsby-transformer-xml/"}}

